import { Component, h, Element, Prop } from '@stencil/core';

@Component({
  tag: 'ks-order-login',
})
export class OrderLogin {

    @Element() root: HTMLElement;

    @Prop() api: string = "";
    @Prop() action: string = "";
    @Prop() passwordMessage: string = "";
    @Prop() passwordRecovery: string = "";

    @Prop() heading: string = "";
    @Prop() login: string = "";
    @Prop() passwordRecoveryString: string = "";
    @Prop() cartProductsMessage: string = "";

    @Prop() promptHeading: string = "";
    @Prop() promptToCart: string = "";
    @Prop() promptToConfirmation: string = "";

    componentDidLoad(){
        this.root.style.display = "block";
    }

    render() {
        return[
            <div class="uk-padding" style={{backgroundColor: "#f2f2f5"}}>
                <div class="uk-text-center" style={{maxWidth: "500px", margin: "auto"}}>
                    <p class="ks-text-decorated uk-text-bold" style={{fontSize: "28px"}}>{this.heading}</p>

                    <form onSubmit={(event)=>this.Submit(event)}>
                        <slot />

                        <ks-button submit name={this.login} />
                    </form>

                    <a href={this.passwordRecovery} style={{marginTop: "10px", display: "block"}}>{this.passwordRecoveryString}</a>
                </div>
            </div>
        ];
    }

    async Submit(event: Event) {
        event.preventDefault();

        const form = this.root.querySelector("form");
        const email = form.querySelector("ks-input-dynamic, ks-input-text[email]") as HTMLKsInputDynamicElement;
        const pass = form.querySelector("ks-input-text[password]") as HTMLKsInputTextElement;

        await email.Validate();
        await pass.Validate();

        if (await email.IsValid() && await pass.IsValid()) {
            var headers = new Headers();
            headers.append('pragma', 'no-cache');
            headers.append('cache-control', 'no-cache');

            const formData = new FormData(form);

            const response = await fetch(this.api, {
                body: formData,
                method: 'POST',
                headers: headers,
                credentials: "same-origin"
            }).then(response => response.text());

            if(response == "false")
                pass.message = this.passwordMessage;

            else if(response == "true") {
                this.LoginPrompt(formData);
            }
        }
    }

    async LoginPrompt(data: FormData) {
        
        var headers = new Headers();
        headers.append('pragma', 'no-cache');
        headers.append('cache-control', 'no-cache');

        const formData = new FormData();
        formData.set("data", `email=${encodeURIComponent(data.get("email").toString())}&haslo=${encodeURIComponent(data.get("haslo").toString())}`)

        const response = await fetch(this.action, {
            body: formData,
            method: 'POST',
            headers: headers,
            credentials: "same-origin"
        }).then(response => response.text());

        const prompt = document.createElement("ks-order-login-prompt");
        prompt.setAttribute("heading", this.promptHeading);
        prompt.setAttribute("to-cart", this.promptToCart);
        prompt.setAttribute("to-confirmation", this.promptToConfirmation);
    
        if(response.search("koszyk.html") != -1) {
            prompt.setAttribute("message", this.cartProductsMessage);
        }
        
        document.body.appendChild(prompt);
    }
}